<template>
  <div id="about">
    <common-header title="关于我们"></common-header>
    <div class="about__logo">
      <img src="../../../assets/about_logo.png" alt="">
      <div class="logo__info">版本号: 6.0.3</div>
    </div>
    <div class="about__link">
      <Cell title="关于我们" @click="linkTo('AboutUs')"/>
      <Cell title="用户协议" @click="linkTo('User')"/>
      <Cell title="玩法" @click="linkTo('Game')"/>
      <Cell title="联络我们" @click="linkTo('Contact')"/>
    </div>

    <div class="about__copy">
      <p>@Copyright 2016-2017 youdeman ALL Rights</p>
      <p>广州何人谈网络信息开发有限公司 版权所有</p>
    </div>
  </div>
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
    components: {
      Cell
    }
  }
</script>

<style lang="scss">
  #about {
    position: relative;
    height: 100%;
    background: #f0f0f0;

    .about__logo {
      padding: 1.06rem 0 ;
      text-align: center;
      img {
        width: 2.67rem;
        height: 2.67rem;
      }

      .logo__info {
        padding: 0.2rem 0;
        font-size: 0.427rem;
        color: #de7fef;
      }
    }

    .about__copy {
      position: absolute;
      bottom: 0.2rem;
      width: 100%;
      color: #999;
      font-size: 0.3467rem;
      text-align: center;
    }
  }
</style>



